.ourteam {
	padding-bottom: 40px;
	background: url("../img/img-3.jpg") 0 0 repeat-y;
	&-title {
		margin-bottom: 70px;
		position: relative;
		text-align: center;
		color: #fff;
		&:before {
			content: '';
			position: absolute;
			width: 45px;
			height: 1px;
			bottom: -10px;
			left: 0;
			right: 0;
			margin: 0 auto;
			background-color: #fff;
		}
	}
	&-list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
}
.ourteam-item {
	width: 100%;
	max-width: 270px;
	min-height: 318px;
	cursor: pointer;
	
	img {
		display: block;
		width: 100%;
	}
	&.active {
		& .ourteam-info {
			height: 100%;
			transition:  0.3s;
			&__text {
				display: block;
				transition: 0.3s;
			}
		}
		img {
			display: none;
		}
	}
}
.ourteam-info {
	padding: 15px 20px;
	background-color: #fff;
	overflow: hidden;
	transition:  0.3s;
	&__post {
		font-family: @oswaldFont;
		font-size: 12px;
		line-height: 24px;
		color: #afafaf;
		text-transform: uppercase;
	}
	&__text {
		display: none;
		transition: 0.3s;
		user-select: none;
	}
}